<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-lx-cascades"></i> 
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="handle-box">
        <el-button
          type="primary"
          icon="el-icon-delete"
          class="handle-del mr10"
          @click="delAllSelection"
          ></el-button
        >
        <el-input
          v-model="query.name"
          placeholder=""
          class="handle-input mr10"
        ></el-input>
        <el-button type="primary" icon="el-icon-search" @click="handleSearch"
          ></el-button
        >
        <el-button type="primary" icon="el-icon-plus" @click="addUser"
          ></el-button
        >
      </div>
      <el-table
        :data="tableDataEnd"
        border
        class="table"
        ref="multipleTable"
        header-cell-class-name="table-header"
        @selection-change="handleSelectionChange"
      >
        <el-table-column
          type="selection"
          width="55"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="id"
          label="ID"
          width="55"
          align="center"
        ></el-table-column>
        <el-table-column prop="name" label="">aa</el-table-column>
        <el-table-column prop="permission" label=""></el-table-column>
        <el-table-column prop="sex" label=""></el-table-column>
        <el-table-column
          prop="record"
          label=""
          align="center"
        ></el-table-column>
        <el-table-column prop="date" label=""></el-table-column>
        <el-table-column label="" width="180" align="center">
          <template slot-scope="scope">
            <el-button
              type="text"
              icon="el-icon-edit"
              @click="handleEdit(scope.$index, scope.row)"
              ></el-button
            >
            <el-button
              type="text"
              icon="el-icon-delete"
              class="red"
              @click="handleDelete(scope.$index, scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
          background
          layout="total, prev, pager, next"
          :current-page="query.pageIndex"
          :page-size="query.pageSize"
          :total="pageTotal"
          @current-change="handlePageChange"
        ></el-pagination>
      </div>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="editVisible" width="30%">
      <el-row :model="form" label-width="70px">
        <el-col :span="12"> {{ form.name }} </el-col>
        <el-col :span="12"> :{{ form.sex }} </el-col>
        <el-col :span="12"> {{ form.phone }} </el-col>
        <el-col :span="12"> :{{ form.birth }} </el-col>
        <el-col :span="12"> {{ form.address }} </el-col>
        <el-col :span="12"> :{{ form.record }} </el-col>
        <el-col :span="24"> :{{ form.selfDesc }} </el-col>
        <el-col :span="24"> {{ form.date }} </el-col>
        <el-col :span="24"> :{{ form.alterDate }} </el-col>
        <el-col :span="24"> ID:{{ form.alterAdmin }} </el-col>
      </el-row>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="editVisible = false"> </el-button>
        <!-- <el-button type="primary" @click="saveEdit"> </el-button> -->
      </span>
    </el-dialog>

    <el-dialog title="" :visible.sync="addVisible" width="40%">
        <el-form
      ref="form"
      :model="form"
      status-icon
      :rules="rules"
      label-width="100px"
      class="login-form demo-ruleForm"
      label-position="righ"
    >
        <el-form-item label="ID" placeholder="ID">
        <el-input v-model="form.ID" maxlength="30" show-word-limit> </el-input>
      </el-form-item>

      <el-form-item label="" placeholder="">
        <el-input v-model="form.name" maxlength="30" show-word-limit>
        </el-input>
      </el-form-item>

      <el-form-item label="" placeholder="">
        <el-input v-model="form.eamil" maxlength="30"> </el-input>
      </el-form-item>

      <el-form-item prop="phone" label="" placeholder="">
        <el-input v-model="form.phone"> </el-input>
      </el-form-item>

      <el-form-item label="" placeholder="" prop="password">
        <el-input
          type="password"
          v-model="form.password"
          autocomplete="off"
          maxlength="30"
          show-password
        >
        </el-input>
      </el-form-item>

      <el-form-item label="" prop="password2">
        <el-input
          type="password"
          v-model="form.password2"
          autocomplete="off"
          maxlength="30"
        ></el-input>
      </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="addVisible = false"> </el-button>
        <el-button type="primary" @click="saveAdd">   </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "userlist",
  data() {
// 
    const reg = /(?=.*[A-Z])/;
    // 
    const regNumber = /(?=.*[\d])/;
    // 
    const regLetter = /(?=.*[a-z])/;
    // 
    const regCharacter = /(?=.*[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'\\[\]~@#%&*\-+={}|])/;
    // 
    const validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (value.length > 16) {
          callback(new Error("16"));
        } else if (value.length < 8) {
          callback(new Error("8"));
        } else {
          if (!/^[a-zA-Z\d]{1}/.test(value)) {
            callback(new Error(""));
          } else {
            if (!regNumber.test(value)) {
              callback(
                new Error("!")
              );
            } else if (!regLetter.test(value)) {
              callback(
                new Error("!")
              );
            } else if (!regCharacter.test(value)) {
              callback(
                new Error("!")
              );
            } else if (!reg.test(value)) {
              callback(
                new Error("!")
              );
            } else {
              callback();
            }
          }
        }
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.form.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };

    return {
      form: {
        name: "",
        ID: "",
        eamil: "",
        phone: "", //
        verifyCode: "", //
        btnTitle: "",
        password: "",
        password2: "",
      },
      rules: {
        password: [{ validator: validatePass, trigger: "blur" }],
        password2: [{ validator: validatePass2, trigger: "blur" }],
        phone: [
          {
            required: true,
            pattern: /^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/,
            message: "",
            trigger: "change", //
          },
        ],
      },
      query: {
        address: "",
        name: "",
        pageIndex: 1,
        pageSize: 10,
      },
      tableDataBegin: [],
      tableDataEnd: [],
      multipleSelection: [],
      delList: [],
      editVisible: false,
      addVisible: false,
      pageTotal: 0,
      form: {},
      idx: -1,
      id: -1,
    };
  },
  mounted() {
    this.axios({
      method: "get",
      url: "../../../static/mock/user.json",
      //url: "/user.json",
      data: "",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
      },
    })
      .then((response) => {
        this.tableDataBegin = response.data.userlist;
        this.pageTotal = this.tableDataBegin.length;
        if (this.pageTotal > this.query.pageSize) {
          //
          for (let index = 0; index < this.query.pageSize; index++) {
            this.tableDataEnd.push(this.tableDataBegin[index]);
          }
        } else {
          this.tableDataEnd = this.tableDataBegin;
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    //
    saveAdd() {
        this.$refs.form.validate((valid) => {
        if (valid) {
          this.axios({
            method: "post",
            url: "",
            data: this.$qs.stringify(this.form),
            headers: {
              "Content-Type":
                "application/x-www-form-urlencoded; charset=UTF-8",
            },
          })
            .then((response) => {
              console.log(response+"--------------------------------------------------");
                //




              this.addVisible = false;
            })
            .catch((error) => {
              console.log(error+"--------------------------------------------------");
            });
        } else {
          console.log("--------------------------------------------------");
          return false;
        }
      });
    },
    //
    addUser() {
      this.addVisible = true;
    },
    // 
    handleSearch() {
      this.$set(this.query, "pageIndex", 1);
      this.getData();
    },
    // 
    handleDelete(index, row) {
      // 
      this.$confirm("", "", {
        type: "warning",
      })
        .then(() => {
          this.$message.success("");
          this.tableData.splice(index, 1);
        })
        .catch(() => {});
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    delAllSelection() {
      const length = this.multipleSelection.length;
      let str = "";
      this.delList = this.delList.concat(this.multipleSelection);
      for (let i = 0; i < length; i++) {
        str += this.multipleSelection[i].name + " ";
      }
      this.$message.error(`${str}`);
      this.multipleSelection = [];
    },
    // 
    handleEdit(index, row) {
      this.idx = index;
      this.form = row;
      this.editVisible = true;
    },
    // 
    // saveEdit() {
    //     this.editVisible = false;
    //     this.$message.success(` ${this.idx + 1} `);
    //     this.$set(this.tableData, this.idx, this.form);
    // },
    // 
    handlePageChange(val) {
      this.$set(this.query, "pageIndex", val);
      this.getData();
    },
  },
};
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.handle-select {
  width: 120px;
}

.handle-input {
  width: 300px;
  display: inline-block;
}
.table {
  width: 100%;
  font-size: 14px;
}
.red {
  color: #ff0000;
}
.mr10 {
  margin-right: 10px;
}
.table-td-thumb {
  display: block;
  margin: auto;
  width: 40px;
  height: 40px;
}
</style>



