<template>
  <div id="div3">
    <div id="div2">
      <div id="div1">
        <div style="padding-top: 25px">
          <el-input
            v-model="tableDataName"
            placeholder=""
            style="width: 360px"
          ></el-input>
          <el-button type="primary" @click="doFilter"></el-button>
        </div>

        <el-table :data="tableDataEnd" border style="margin-top: 25px">
          <el-table-column prop="book_name" label="" width="240">
          </el-table-column>
          <el-table-column prop="book_lend_date" label="" width="200">
          </el-table-column>
          <el-table-column prop="book_scheduled" label="" width="200">
          </el-table-column>
          <el-table-column prop="book_return_date" label="" width="200">
          </el-table-column>
          <el-table-column prop="book_return_state" label="" width="100">
          </el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <!-- scopetableData,scope.row scope.$index -->
              <el-button type="info" @click="info(scope.$index, scope.row)"
                ></el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[1, 2, 3, 4, 5, 6]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next"
          :total="totalItems"
          style="margin-top: 25px"
        >
        </el-pagination>
        <el-dialog
          title=""
          :visible.sync="dialogTableVisible"
          width="720px"
        >
          <el-table :data="table2">
            <el-table-column
              prop="user_name"
              label=""
              width="80"
            ></el-table-column>
            <el-table-column
              prop="user_id"
              label="ID"
              width="60"
            ></el-table-column>
            <el-table-column
              prop="book_name"
              label=""
              width="160"
            ></el-table-column>
            <el-table-column
              prop="book_lend_date"
              label=""
              width="120"
            ></el-table-column>
            <el-table-column
              prop="book_scheduled"
              label=""
              width="120"
            ></el-table-column>
            <el-table-column label="" width="120">{{
              this.lastday
            }}</el-table-column>
          </el-table>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>

import Vue from "vue";
export default {
  data() {
    return {
      
      tableDataBegin: [],
      tableDataName: "",
      tableDataEnd: [], //
      table2: [], //
      lastday: "",
      currentPage: 1,
      pageSize: 2,
      totalItems: 0,
      filterTableDataEnd: [],
      flag: false,
      dialogTableVisible: false,
      returnState: [
        {
          value: "ture",
          label: "",
        },
        {
          value: "false",
          label: "",
        },
      ],
    };
  },
  mounted() {
    Vue.axios({
      method: "get",
      url:'http://10.10.102.142:8080/record/getRecordById',
      // url: "../../../static/mock/rentList.json",
      params: {id: window.sessionStorage.getItem("ms_userid")},
      headers: {
        "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
      },
    })
      .then((response) => {
        this.tableDataBegin = [];
        console.log(response.data);
        this.tableDataBegin = response.data;
        console.log(this.tableDataEnd.length);
        this.totalItems = this.tableDataBegin.length;
        if (this.totalItems > this.pageSize) {
          //
          for (let index = 0; index < this.pageSize; index++) {
            this.tableDataEnd.push(this.tableDataBegin[index]);
          }
        } else {
          this.tableDataEnd = this.tableDataBegin;
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    //,
    //currentChangePage
    doFilter() {
      if (this.tableDataName == "") {
        this.$message.warning("");
        return;
      }
      this.tableDataEnd = [];
      //,
      this.filterTableDataEnd = [];
      this.tableDataBegin.forEach((value, index) => {
        if (value.book_name) {
          if (value.book_name.indexOf(this.tableDataName) >= 0) {
            this.filterTableDataEnd.push(value);
          }
        }
      });
      //
      this.currentPage = 1;
      this.totalItems = this.filterTableDataEnd.length;
      //,
      this.currentChangePage(this.filterTableDataEnd);
      //
      this.flag = true;
    },
    handleSizeChange(val) {
      console.log(` ${val} `);
      this.pageSize = val;
      this.handleCurrentChange(this.currentPage);
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.currentPage = val;
      //
      if (!this.flag) {
        //tableDataBegintableDataEnd
        this.currentChangePage(this.tableDataBegin);
      } else {
        //
        this.currentChangePage(this.filterTableDataEnd);
      }
    }, //
    currentChangePage(list) {
      //
      let from = (this.currentPage - 1) * this.pageSize;
      let to = this.currentPage * this.pageSize;
      this.tableDataEnd = [];
      for (; from < to; from++) {
        if (list[from]) {
          this.tableDataEnd.push(list[from]);
        }
      }
    },
    info(index, row) {
      this.table2 = [];
      this.table2.push(this.tableDataEnd[index]);
      this.dialogTableVisible = true;
    },
  },
};
</script>

<style scoped>
#div1 {
  padding-left: auto;
  padding-right: auto;
  margin: auto;
  width: 90%;
}
#div2 {
  margin: auto;
  background-color: rgba(207, 235, 247, 0.8);
  width: 90%;
  padding-bottom: 50px;
}
#div3 {
  background-image: url(../../assets/img/login-bg.jpg);
  background-size: 100%;
  height: 780px;
}
</style>


