<template>
  <div>
    <el-input v-model="tableDataName" placeholder="" style="width:240px"></el-input>
    <el-button type="primary"  @click="doFilter()"></el-button>
    <el-table :data="tableDataEnd" border style="margin-top: 25px">
          <el-table-column prop="book_name" label="" width="240">
          </el-table-column>
          <el-table-column prop="user_name" label="" width="200">
          </el-table-column>
          <el-table-column prop="book_lend_date" label="" width="200">            
          </el-table-column>
          <el-table-column prop="book_return_date" label="" width="200">            
          </el-table-column>
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <!-- scopetableData,scope.row scope.$index -->
              <el-button type="info" @click="info(scope.$index, scope.row)"
                ></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[1, 2, 3, 4, 5, 6]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next"
          :total="totalItems"
          style="margin-top: 25px"
        >
        </el-pagination>
        <el-dialog
          title=""
          :visible.sync="dialogTableVisible"
          width="720px"
        >
          <el-row :model="bookDetail" label-width="70px">
        <el-col :span="12"> {{ bookDetail.books_name }} </el-col>
        <el-col :span="12"> :{{ bookDetail.books_author }} </el-col>
        <el-col :span="12"> {{ bookDetail.info_country }} </el-col>
        <el-col :span="12"> :{{ bookDetail.info_theme }} </el-col>
        <el-col :span="12"> {{ bookDetail.books_last }} </el-col>
        <el-col :span="24"> :{{ bookDetail.books_registerDate }} </el-col>
      </el-row>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogTableVisible = false"> </el-button>
        
      </span>
        </el-dialog>
  </div>
</template>
<script>
import Vue from "vue";
  export default {
    data() {
      return {
        tableDataBegin: [],//
        tableDataName: "",
        tableDataEnd: [],//
        table2:[],//
        currentPage: 1,
        pageSize: 4,
        totalItems: 0,
        filterTableDataEnd:[],
        flag:false,
        dialogTableVisible: false,
        bookDetail:[]
      };
    },
    mounted() {
    Vue.axios({
      method: "get",
      url: "http://10.10.102.142:8080/record/getAll",
      data: "",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
      },
    })
      .then((response) => {
        console.log(response.data)
        this.tableDataBegin = [];
        this.tableDataBegin = response.data;
        this.totalItems = this.tableDataBegin.length;
        if (this.totalItems > this.pageSize) {
          //
          for (let index = 0; index < this.pageSize; index++) {
            this.tableDataEnd.push(this.tableDataBegin[index]);
          }
        } else {
          this.tableDataEnd = this.tableDataBegin;
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
    methods: {
      //,
      //currentChangePage
    //   doFilter() {
    //     Vue.axios({
    //   method: "get",
    //   url: "http://10.10.102.143:8080/books/quaryName",
    //   params: {name:this.tableDataName},
    //   headers: {
    //     "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
    //   },
    // })
    //   .then((response) => {
    //     console.log(response.data);
    //     this.tableDataBegin = [];
    //     this.tableDataEnd =[];
    //     this.tableDataBegin = response.data.object;
    //     this.totalItems = this.tableDataBegin.length;
    //     if (this.totalItems > this.pageSize) {
    //       //
    //       for (let index = 0; index < this.pageSize; index++) {
    //         this.tableDataEnd.push(this.tableDataBegin[index]);
    //       }
    //     } else {
    //       this.tableDataEnd = this.tableDataBegin;
    //     }
    //   })
    //   .catch((error) => {
    //     console.log(error);
    //   });
      // },

      doFilter() {
      if (this.tableDataName == "") {
        this.$message.warning("");
        return;
      }
      this.tableDataEnd = [];
      //,
      this.filterTableDataEnd = [];
      this.tableDataBegin.forEach((value, index) => {
        if (value.book_name) {
          if (value.book_name.indexOf(this.tableDataName) >= 0) {
            this.filterTableDataEnd.push(value);
          }
        }
      });
      //
      this.currentPage = 1;
      this.totalItems = this.filterTableDataEnd.length;
      //,
      this.currentChangePage(this.filterTableDataEnd);
      //
      this.flag = true;
    },

      handleSizeChange(val) {
        console.log(` ${val} `);
        this.pageSize = val;
        this.handleCurrentChange(this.currentPage);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.currentPage = val;
        //
        if(!this.flag){
        //tableDataBegintableDataEnd
          this.currentChangePage(this.tableDataBegin);
        }else{
          this.currentChangePage(this.filterTableDataEnd);
        }
      }, //
      currentChangePage(list) {
        let from = (this.currentPage - 1) * this.pageSize;
        let to = this.currentPage * this.pageSize;
        this.tableDataEnd = [];
        for (; from < to; from++) {
          if (list[from]) {
            this.tableDataEnd.push(list[from]);
          }
        }
      },
       info(index, row) {
         console.log(row.book_id);
         Vue.axios({
      method: "get",
      url: "http://10.10.102.142:8080/books/getBookAllInfo",
      params: {id:row.book_id},
      headers: {
        "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
      },
    })
      .then((response) => {
        console.log(response.data)
        this.bookDetail = response.data.object;
      })
      .catch((error) => {
        console.log(error);
      });
      this.dialogTableVisible = true;
    },
    }
  };
</script>


