<template>
  <div class="box">
    <div class="background">
      <img :src="imgSrc" width="100%" height="100%" alt="" />
    </div>
    <el-form
      :inline="true"
      :model="formInline"
      class="demo-form-inline"
      label-width="60px"
    >
      <el-form-item label="">
        <el-select v-model="formInline.info_country" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="formInline.info_type"  placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="formInline.info_length" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value="1"></el-option>
          <el-option label="" value="2"></el-option>
          <el-option label="" value="3"></el-option>
          <el-option label="" value="4"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="">
        <el-select v-model="formInline.info_theme" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="onSubmit()" class="firstSearch"
          ></el-button
        >
      </el-form-item>
    </el-form>

    <el-input
      v-model="input"
      placeholder=""
      class="searchBox"
    ></el-input>
    <el-button
      type="primary"
      icon="el-icon-search"
      class="searchButton"
      @click="inittable()"
      ></el-button
    >

    <el-table :data="tableDataEnd" style="width: 90%" class="detail">
      <el-table-column
        fixed
        prop="books_createTime"
        label=""
        width="150"
      >
      </el-table-column>
      <el-table-column prop="books_name" label="" width="120">
      </el-table-column>
      <el-table-column prop="books_author" label="" width="120">
      </el-table-column>
      <el-table-column prop="books_last" label="" width="120">
      </el-table-column>

      <el-table-column prop="books_id" label="ID" width="120">
      </el-table-column>
      <el-table-column fixed="right" label="" width="120">
        <template slot-scope="scope">
          <!-- <el-button slot="reference" @click="bookDetail(scope.row)"
            ></el-button> -->

          <el-button type="text" @click="bookDetail(scope.row)"></el-button>

          <el-button type="text" @click="open(scope.row)"></el-button>
        </template>

        <!-- <el-popover placement="left" width="600px" trigger="click">
          <el-table :data="gridData">
            <el-table-column width="600" label="">
              {{info_country}} {{books_type}}<br>
             {{info_length}} {{info_theme}}
            </el-table-column>
          </el-table>
        </el-popover> -->
      </el-table-column>

      <!-- <el-table-column fixed="right" label="" width="120">
        <template slot-scope="scope">
          <el-button type="text" @click="open(scope.row)"></el-button>
        </template>
      </el-table-column> -->
    </el-table>

    <div>
      <el-pagination
        class="page"
        background
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        layout="prev, pager, next"
        :total="totalItems"
        :page-size="pageSize"
      >
      </el-pagination>
    </div>

    <el-dialog title="" :visible.sync="detailVisible" width="30%">
      <el-row label-width="70px" >
        <el-col :span="12"> {{ this.booksInfo.info_country }} </el-col>
        <el-col :span="12"> : {{ this.booksInfo.info_type }} </el-col>
        <el-col :span="12"> {{ this.booksInfo.info_length }} </el-col>
        <el-col :span="12"> : {{ this.booksInfo.info_theme }} </el-col>
      </el-row>
      <div slot="footer" class="dialog-footer">
        <el-button @click="detailVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>


<script>
import Vue from 'vue'
export default {
  
  data() {
    return {
      gridData: [
        {
          description: "",
        },
      ],
      imgSrc: require("../../assets/img/login-bg.jpg"),
      tableDataEnd: [],
      tableDataBegin: [],
      input: "",
      detailVisible: false,
      totalItems: 0,
      currentPage: 0,
      pageSize: 8,
      booksInfo :{},
      formInline: {
        country: "",
        type: "",
        length: "",
        theme: "",
        
      },
    };
  },
  mounted() {
    this.axios({
      method: "get",
      url: "http://10.10.102.142:8080/books/booksList",
      //url: "/user.json",
      data: {},
      headers: {
        "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
      },
    })
      .then((response) => {
        console.log(response);
        let array = response.data;
        this.totalItems = array.length;
        for (let i = 0; i < this.totalItems; i++) {
          this.tableDataBegin[i] = array[i].book;
        }

        if (this.totalItems > this.pageSize) {
          //
          for (let index = 0; index < this.pageSize; index++) {
            this.tableDataEnd.push(this.tableDataBegin[index]);
          }
        } else {
          this.tableDataEnd = this.tableDataBegin;
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods: {
    open(row) {
      // console.log(row.books_id);
      // console.log(row);
      // console.log(window.sessionStorage.getItem("ms_userid"));
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.axios({
            method: "post",
            url: "http://10.10.102.142:8080/record/insertNotReturn",
            //url: "/user.json",
            data: {
              book_id: row.books_id,
              book_name: row.books_name,
              user_id: window.sessionStorage.getItem("ms_userid"),
              user_name: window.sessionStorage.getItem("ms_username"),
              book_return_state:0, 
              book_lend_date:0
            },
            headers: {
              "Content-Type": "application/json; charset=UTF-8",
            },
          }).then((response) => {
            console.log(response);
            if (response.data.state == 406) {
              this.$message({
                type: "warn",
                message: "!",
              });
            } else if (response.data.state == 200) {
              this.$message({
                type: "success",
                message: "!",
              });
            }
          });
        })
        .catch(() => {});
    },

    onSubmit() {
      this.axios({
        method: "post",
        url: "http://10.10.102.142:8080/books/quaryType",
        //url: "/user.json",
        data: {
              info_theme:this.formInline.info_theme,
              info_type:this.formInline.info_type,
              info_length:this.formInline.info_length,
              info_country:this.formInline.info_country
            },
        headers: {
          "Content-Type": "application/json; charset=UTF-8",
        },
      })
      .then((response) => {
          this.tableDataEnd = [];
          this.tableDataBegin = [];
          console.log(response);
          this.tableDataBegin = response.data.object;
          console.log(this.tableDataBegin);
          this.totalItems = this.tableDataBegin.length;
          if (this.totalItems > this.pageSize) {
            //
            for (let index = 0; index < this.pageSize; index++) {
              this.tableDataEnd.push(this.tableDataBegin[index]);
            }
          } else {
            this.tableDataEnd = this.tableDataBegin;
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },

    inittable() {
      this.axios({
        method: "get",
        url: "http://10.10.102.142:8080/books/quaryName",
        //url: "/user.json",
        params: {
          name: this.input,
        },
        headers: {
          "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
        },
      })
        .then((response) => {
          this.tableDataEnd = [];
          this.tableDataBegin = [];
          console.log(response);
          this.tableDataBegin = response.data.object;
          console.log(this.tableDataBegin);
          this.totalItems = this.tableDataBegin.length;
          if (this.totalItems > this.pageSize) {
            //
            for (let index = 0; index < this.pageSize; index++) {
              this.tableDataEnd.push(this.tableDataBegin[index]);
            }
          } else {
            this.tableDataEnd = this.tableDataBegin;
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
    bookDetail(row) {
      console.log(row.books_id);
      this.axios({
        method: "get",
        url: "http://10.10.102.142:8080/books/detail",
        params: {
          books_id: row.books_id,
        },
        headers: {
          "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8",
        },
      })
        .then((response) => {
           this.booksInfo = {};
          console.log(response.data.object);
          this.booksInfo = response.data.object.books_info;
        })
        .catch((error) => {
          console.log(error);
        });
      this.detailVisible = true;
      
      
    },
    handleCurrentChange(val) {
      console.log(`: ${val}`);
      this.currentPage = val;
      //tableDataBegintableDataEnd
      this.currentChangePage(this.tableDataBegin);
    }, //
    currentChangePage(list) {
      //
      let from = (this.currentPage - 1) * this.pageSize;
      let to = this.currentPage * this.pageSize;
      this.tableDataEnd = [];
      for (; from < to; from++) {
        if (list[from]) {
          this.tableDataEnd.push(list[from]);
        }
      }
    },
  },
};
</script>

<style>
.background {
  width: 100%;
  height: 100%; /**100% */
  /*index: -1;*/
  position: absolute;
}

.firstSearch {
  opacity: 0.7;
}

.demo-form-inline {
  padding: 35px 0 0 0;
}

.searchBox {
  width: 300px;
  padding: 0 0 0 60px;
  margin: 5px;
  opacity: 0.8;
}
.searchButton {
  margin: 0;
  border: 0;
  opacity: 0.7;
}

.detail {
  left: 60px;
  opacity: 0.8;
  border-radius: 10px;
}
.page {
  padding: 0 0 0 65px;
  margin: 10px;
  opacity: 0.7;
}
</style>




