<template>
  <div class="login-container">
    <el-form
      ref="form"
      :model="form"
      status-icon
      :rules="rules"
      label-width="100px"
      class="login-form demo-ruleForm"
      label-position="righ"
    >
      <h2 class="login-title"></h2>

      <el-form-item label="" placeholder="" prop="user_name">
        <el-input v-model="form.user_name" maxlength="30" show-word-limit @blur="checkUsernameExist()">
        </el-input>
      </el-form-item>

      <el-form-item label="" placeholder="" prop="user_email">
        <el-input v-model="form.user_email" maxlength="30"> </el-input>
      </el-form-item>

      <el-form-item label="" prop="user_sex">
        <el-select
          style="width: 100%"
          v-model="form.user_sex"
          placeholder=""
        >
          <el-option label="" value="1"></el-option>
          <el-option label="" value="0"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" required>
        <el-form-item prop="user_birthday">
          <el-date-picker
            type="date"
            placeholder=""
            v-model="form.user_birthday"
            style="width: 100%"
          ></el-date-picker>
        </el-form-item>
      </el-form-item>

      <el-form-item prop="user_address" label="" placeholder="">
        <el-input v-model="form.user_address" maxlength="30"> </el-input>
      </el-form-item>

      <el-form-item prop="user_phone" label="" placeholder="">
        <el-input v-model="form.user_phone" @blur="checkPhoneExist()">
        </el-input>
        <el-button :disabled="msg_isAbled" @click="getVerrifyCode">{{
          form.btnTitle
        }}</el-button>
      </el-form-item>

      <el-form-item prop="telCode" label="" placeholder="">
        <el-input v-model="form.verifyCode"> </el-input>
      </el-form-item>

      <el-form-item
        label=""
        placeholder=""
        prop="user_password"
      >
        <el-input
          type="password"
          v-model="form.user_password"
          autocomplete="off"
          maxlength="30"
          show-password
        >
        </el-input>
      </el-form-item>

      <el-form-item label="" prop="checkPassword">
        <el-input
          type="password"
          v-model="form.checkPassword"
          autocomplete="off"
          maxlength="30"
        ></el-input>
      </el-form-item>

      <div class="register-btn">
        <el-button type="primary" @click="onSubmit()"></el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    // 
    const reg = /(?=.*[A-Z])/;
    // 
    const regNumber = /(?=.*[\d])/;
    // 
    const regLetter = /(?=.*[a-z])/;
    // 
    const regCharacter = /(?=.*[`~!@#$%^&*()_\-+=<>?:"{}|,.\/;'\\[\]~@#%&*\-+={}|])/;
    // 
    const validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (value.length > 16) {
          callback(new Error("16"));
        } else if (value.length < 8) {
          callback(new Error("8"));
        } else {
          if (!/^[a-zA-Z\d]{1}/.test(value)) {
            callback(new Error(""));
          } else {
            if (!regNumber.test(value)) {
              callback(
                new Error("!")
              );
            } else if (!regLetter.test(value)) {
              callback(
                new Error("!")
              );
            } else if (!regCharacter.test(value)) {
              callback(
                new Error("!")
              );
            } else if (!reg.test(value)) {
              callback(
                new Error("!")
              );
            } else {
              callback();
            }
          }
        }
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.form.user_password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      submit_isAbled : false,//?????
      msg_isAbled: true,
      form: {
        user_name: "",
        user_email: "",
        user_sex: "",
        user_birthday: "",
        user_phone: "", //
        verifyCode: "", //
        user_address: "",
        user_password: "",
        checkPassword: "",
        btnTitle: ""
      },
      realVerifyCode: "",
      rules: {
        verifyCode: [{ required: true, message: "", trigger: "blur" }],
        user_name: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 20,
            message: " 3  20 ",
            trigger: "blur"
          }
        ],
        user_password: [
          { validator: validatePass, trigger: "blur" },
          { required: true, message: "", trigger: "blur" }
        ],
        checkPassword: [
          { validator: validatePass2, trigger: "blur" },
          { required: true, message: "", trigger: "blur" }
        ],
        user_address: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        user_email: [
          { type: "email", required: true, trigger: "change" },
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        user_sex: [{ required: true, message: "", trigger: "blur" }],
        user_birthday: [
          {
            type: "date",
            format: "yyyy-MM-dd",
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        user_phone: [
          {
            required: true,
            pattern: /^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/,
            message: "",
            trigger: "change" //
          },
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ]
      },
      verifyCode: null
    };
  },
  props: {
    type: {
      type: String,
      default: "text"
    },
    placeholder: String,
    value: String,
    name: String,
    btnTitle: String //input
  },
  methods: {
    checkUsernameExist() {
      let me=this;
      this.axios({
        method: "get",
        url: "http://10.10.102.142:8080/user/checkUsernameIsExist",
        params: {
          user_name: me.form.user_name
        }
      })
        .then(response => {
          if (response.data.code == 406) {
            console.log("---- ");
            me.submit_isAbled = false;
            alert("");
          } else {
            me.submit_isAbled = true;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    checkPhoneExist() {
      this.axios({
        method: "get",
        url: "http://10.10.102.142:8080/user/checkPhoneIsExist",
        params: {
          user_phone: this.form.user_phone
        }
      })
        .then(response => {
          if (response.data.code == 406) {
            this.msg_isAbled = true;
            alert("");
          } else {
            this.msg_isAbled = false;
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    onSubmit() {
      let me=this;
      this.$refs.form.validate(valid => {
        if(!me.submit_isAbled){
          alert("");
          return false;
        }
        if (valid) {
          console.log(this.realVerifyCode);
          if (this.realVerifyCode == this.form.verifyCode) {
            console.log(this.form);
            this.axios({
              method: "post",
              url: "http://10.10.102.142:8080/user/register",
              params: {
                user_name: this.form.user_name,
                user_email: this.form.user_email,
                user_sex: this.form.user_sex,
                user_birthday: this.form.user_birthday,
                user_phone: this.form.user_phone, //
                verifyCode: this.form.verifyCode, //
                user_address: this.form.user_address, //
                user_password: this.form.user_password,
                checkPassword: this.form.checkPassword
              },
              headers: {
                "Content-Type":
                  "application/x-www-form-urlencoded; charset=UTF-8"
              }
            })
              .then(response => {
                console.log(
                  response +
                    "-------------------------------------------------"
                );
                this.open();
              })
              .catch(error => {
                console.log(
                  error +
                    "--------------------------------------------------"
                );
              });
          } else {
            alert(
              "--------------------------------------------------"
            );
          }
        } else {
          console.log(
            "--------------------------------------------------"
          );
          return false;
        }
      });
    },
    open() {
      this.$alert(
        "",
        "",
        {
          confirmButtonText: "",
          callback: action => {
            this.$router.push("/");
          }
        }
      );
    },
    getVerrifyCode() {
      //user_phone
      this.$refs.form.validate(valid => {
        if (valid) {
          //axiosuser_phone
          this.axios({
            method: "get",
            url: "http://10.10.102.142:8080/message/send",
            params: {
              user_phone: this.form.user_phone
            },
            headers: {
              "Content-Type": "application/x-www-form-urlencoded; charset=UTF-8"
            }
          })
            .then(response => {
              console.log(response.data.object);
              this.realVerifyCode = response.data.object;
              //
              let time = 60;
              let timer = setInterval(() => {
                if (time == 0) {
                  clearInterval(timer);
                  this.form.btnTitle = "";
                  this.msg_isAbled = false;
                } else {
                  this.form.btnTitle = time + "";
                  this.msg_isAbled = true;
                  time--;
                }
              }, 1000);
            })
            .catch(error => {
              console.log(
                error +
                  "--------------------------------------------------"
              );
            });
        } else {
          console.log(
            "--------------------------------------------------"
          );
          return false;
        }
      });
    }
  }
};
</script>

<style>
.login-form {
  width: 350px;
  margin: 160px auto; /* 160px*/
  background-color: rgb(255, 255, 255, 0.8); /*  */
  padding: 30px;
  border-radius: 20px; /*  */
}

.login-container {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-image: url("../../assets/img/login-bg.jpg");
  background-size: 100%;
}
.register-btn {
  text-align: center;
}
.register-btn button {
  width: 60%;
  height: 36px;
  margin-bottom: 10px;
}
.login-title {
  color: #303133;
  text-align: center;
}

.button {
  background-color: thistle;
  position: absolute;
  left: 1100px;
  top: 560px;
}
</style>



